/* general */

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}

/* Frame and Background */

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.frame {
    height: 762px;
    width: 762px;
    position: relative;
}

.inner {
    position: relative;
}

.game {
    width: 628px;
    height: 577px;
    position: absolute;
    top: 62px;
    left: 67px;
}

.game.night {
    width: 716px;
    top: 6px;
    left: -19px;
}

.hidden {
    display: none;
}

/* buttons */

.buttons {
    position: absolute;
    top: 674px;
    left: 266px;
    height: 71px;
    width: 228px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.icons {
    position: absolute;
    top: 572px;
    left: 194px;
    height: 67px;
    width: 374px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/* poop */

.poop-bag {
    position: absolute;
    top: 300px;
    left: 160px;
}

/* modal */

.modal-inner {
    position: absolute;
    padding: 20px;
    top: 0;
    left: 0;
    right: 0;
    background-color: purple;
    color: white;
    font-size: 20px;
    font-weight: bold;
    border-radius: 12px;
}

.modal:empty {
    display: none;
}

.modal-inner:empty {
    display: none;
}

